@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-index .login-register-form {
  margin: 50px auto;
  padding: 50px;
  max-width: 400px;
  border: 1px solid #e9e9e9;
  background-color: #fff;
  -webkit-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 30px -10px rgba(0, 0, 0, 0.5);
  transition: padding .25s;
}

.main-index .login-register-form .header {
  margin: 0px 0px 20px 0px;
  font-weight: 300;
  font-size: 33px;
  text-align: center;
  color: #b2b3b7;
}

.main-index .login-register-form .small-input-holder:first-child {
  padding-right: 10px;
}

.main-index .login-register-form .small-input-holder:nth-child(2) {
  padding-left: 10px;
}

.main-index .login-register-form .input-group {
  outline: none;
  margin-bottom: 20px;
}

.main-index .login-register-form .input-group .input-group-addon {
  height: 45px;
  background-color: #fff;
  padding: 0px 20px;
  border: 1px solid #e9e9e9;
  border-right: none;
  color: #d6d6d6;
  font-size: 17px;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}

.main-index .login-register-form .input-group .input-group-addon .fa {
  position: absolute;
}

.main-index .login-register-form .input-group .input-group-addon .fa-user {
  top: 14px;
  left: 15px;
}

.main-index .login-register-form .input-group .input-group-addon .fa-key {
  top: 14px;
  left: 13px;
}

.main-index .login-register-form .input-group .form-control {
  height: 45px;
  padding: 20px 5px 20px 15px;
  box-shadow: none;
  border: 1px solid #e9e9e9;
  font-size: 15px;
  font-weight: 300;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.main-index .login-register-form .input-group .form-control::placeholder {
  color: #b2b3b7;
}

.main-index .login-register-form .input-group .form-control:focus {
  border: 1px solid #79a8ff;
}

.main-index .login-register-form .submit-button-holder {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-index .login-register-form .submit-button-holder .forgot-password {
  color: #b2b3b7;
  font-size: 13px;
  text-decoration: none;
}

.main-index .login-register-form .submit-button-holder .submit-btn {
  color: #fff;
  background-color: #4687ff;
  -webkit-box-shadow: 0px 8px 20px -5px #6098ff;
  -moz-box-shadow: 0px 8px 20px -5px #6098ff;
  box-shadow: 0px 8px 20px -5px #6098ff;
  border: none;
  border-radius: 3px;
  height: 45px;
  width: 100%;
  font-size: 13px;
  font-weight: 500;
  transition: all .25s;
  outline: none;
}

.main-index .login-register-form .submit-button-holder .submit-btn:hover {
  background-color: #2d76ff;
  -webkit-box-shadow: 0px 8px 20px -5px #4687ff;
  -moz-box-shadow: 0px 8px 20px -5px #4687ff;
  box-shadow: 0px 8px 20px -5px #4687ff;
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 400px) {
  .main-index .login-register-form {
    padding: 20px 30px 30px 30px;
  }
}
